// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import Subnet from './framework.vue'
import iView from 'iview'
import Vuex from 'vuex'
import VueRouter from 'vue-router'
import axios from 'axios'
import { MainRoute } from './router'
import store from './store'
import './styles/theme.less'
import config from './libs/util'
import particles from 'particles.js/particles'
import ECharts from 'vue-echarts'
import 'echarts/lib/chart/line'
import 'echarts/lib/chart/pie'
import 'echarts/lib/chart/bar'
import 'echarts/lib/component/tooltip'
import 'echarts/lib/component/legend'
import 'echarts/lib/component/title'
import 'echarts/lib/component/title'
import VueCodemirror from 'vue-codemirror'

// require styles
import 'codemirror/lib/codemirror.css'
import 'codemirror/addon/merge/merge.css'
import 'codemirror/addon/merge/merge.js'
import 'codemirror/mode/css/css.js'
import 'codemirror/mode/xml/xml.js'
import 'codemirror/mode/htmlmixed/htmlmixed.js'

import DiffMatchPatch from 'diff-match-patch'
window.diff_match_patch = DiffMatchPatch
window.DIFF_DELETE = -1
window.DIFF_INSERT = 1
window.DIFF_EQUAL = 0



// 引入mockjs
if (process.env.NODE_ENV === 'development') {
  require('./mock/index')

  window.particlesJS.load = function(tag_id, path_config_json, callback){

    /* load json config */
    var xhr = new XMLHttpRequest();
    xhr.open('GET', path_config_json);
    xhr.onreadystatechange = function () {
      if(xhr.readyState == 4){
        if(xhr.status == 200){
          var params = JSON.parse(xhr.response);
          window.particlesJS(tag_id, params);
          if(callback) callback();
        }
      }
    };
    xhr.send();
  };
}


Vue.config.productionTip = false;
Vue.prototype.$config = config;
Vue.use(particles)
Vue.use(Vuex)
Vue.use(iView)
Vue.use(VueRouter)
Vue.prototype.$http = axios
Vue.component('chart', ECharts)
Vue.use(VueCodemirror, /* {
  options: { theme: 'base16-dark', ... },
  events: ['scroll', ...]
} */)
const bus = new Vue();
Vue.prototype.bus = bus;


/* eslint-disable no-new */
const RouterConfig = {
  routes: MainRoute
}

const router = new VueRouter(RouterConfig)

router.beforeEach((to, from, next) => {
  // eslint-disable-next-line
  config.updateBreadcrumb(to.name)
  iView.LoadingBar.start()
  config.title(to.meta.title)
  // eslint-disable-next-line
  console.log(to.matched)
  if (to.matched.length > 0 && to.matched[to.matched.length-1].meta.auth && sessionStorage.getItem('auth') && sessionStorage.getItem('auth').indexOf(to.matched[to.matched.length-1].meta.auth) < 0) {
    router.push({name: 'error_401'})
  }
  if (!sessionStorage.getItem('user') && to.name !== 'login') { // 判断是否已经登录且前往的页面不是登录页
    next(false)
    router.push({name: 'login'})
  } else {
    next()
  }
})

router.afterEach(() => {
  iView.LoadingBar.finish()
  window.scrollTo(0, 0)
})


new Vue({
  el: '#Subnet',
  template: '<Subnet/>',
  components: {Subnet},
  store: store,
  router: router
})

