import request from '@/libs/request';
import { switchDomain } from '@/libs/util'
import util from '@/libs/util'

export function getUserInfo (token) {
  return request({
    url: `${process.env[`VUE_APP_${switchDomain(true)}_SSO_URL`]}passport/account/user_info.json`,
    method: 'get',
    params: {
      'token': token,
      'app': process.env.VUE_APP_SSO_APP_ID,
      'secret': process.env.VUE_APP_SSO_APP_SECRET
    }
  });
}

export function getLocalUserInfo (username, password) {
  return request({
    url: `${process.env[`VUE_APP_${switchDomain(true)}_API_URL`]}${util.url}/login`,
    method: 'post',
    // headers:{'Content-Type':'application/x-www-form-urlencoded'},
    headers:{'Content-Type':'application/json'},
    data: {
      'username': username,
      'password': password
    }
  });
}

export function getUserInfoByToken () {
  return request({
    url: `${process.env[`VUE_APP_${switchDomain(true)}_API_URL`]}${util.url}/user_info`,
    method: 'get',
    headers:{'Content-Type':'application/json'},
  });
}
