<style lang="less" scoped>
  @import "styles/main.less";
</style>
<template>
  <div id="main" class="main" :class="{'main-hide-text': hideMenuText}">
    <Modal v-model="showReLogin" title="登录超时，请重新登录" :closable="false">
      <!--<Form ref="loginForm" :model="formItem">-->
        <!--<FormItem label="密码:">-->
          <!--<Input v-model="formItem.password" placeholder="请输入" type="password"></Input>-->
        <!--</FormItem>-->
      <!--</Form>-->
      <div>请重新登录</div>
      <div slot="footer">
        <Button @click="redirectLogin" :loading="buttonLoading" type="text">退出</Button>
        <!--<Button @click="reLogin" :loading="buttonLoading" type="info">登录</Button>-->
      </div>
    </Modal>
    <!--<div class="sidebar-menu-con" style="width:200px; background:#ffffff" v-if="!hideMenuText">-->
      <!--<sidebar-menu :menuList="menuList" :iconSize="18"/>-->
    <!--</div>-->

    <div class="sidebar-menu-con"
      :style="{width: hideMenuText?'60px':'200px', overflow: hideMenuText ? 'visible' : 'auto', background: '#fff'}"
    >
      <div class="logo-con"></div>
        <sidebar-menu v-if="!hideMenuText" :menuList="menuList" :iconSize="18"/>
        <sidebar-menu-shrink v-else :menuList="menuList"/>
    </div>

    <div class="main-header-con" :style="{paddingLeft: hideMenuText?'60px':'200px'}">
      <div class="main-header">
        <div class="navicon-con" style="display: inline-block">
          <Button
            :style="{transform: 'rotateZ(' + (this.hideMenuText ? '-90' : '0') + 'deg)'}"
            type="text"
            size="small"
            @click="toggleClick"
          >
            <Icon type="md-menu" size="32"></Icon>
          </Button>
        </div>
        <div class="main-breadcrumb" style="display: inline-block">
          <breadcrumb-nav :currentPath="currentPath"></breadcrumb-nav>
        </div>
        <div class="header-avator-con">
          <div class="user-dropdown-menu-con">
            <Row type="flex" justify="end" align="middle" class="user-dropdown-innercon">
              <Dropdown trigger="click" @on-click="handleClickUserDropdown">
                <a href="javascript:void(0)">
                  <Avatar :style="{background: color}">Y</Avatar>
                  <Icon type="md-arrow-dropdown"/>
                </a>
                <DropdownMenu slot="list">
                  <DropdownItem name="loginout">退出登录</DropdownItem>
                </DropdownMenu>
              </Dropdown>
            </Row>
          </div>
        </div>
      </div>
    </div>
    <div class="single-page-con" :style="{paddingLeft: hideMenuText?'60px':'200px'}">
      <div class="single-page">
        <keep-alive>
          <router-view v-if="$route.meta.keepAlive">
          </router-view>
        </keep-alive>
        <router-view v-if="!$route.meta.keepAlive">
        </router-view>
      </div>
    </div>
  </div>
</template>
<script>
    import sidebarMenu from './components/sidebarMenu.vue'
    import sidebarMenuShrink from './components/sidebarMenuShrink.vue'
    import breadcrumbNav from './components/breadcrumbNav.vue'
    import axios from 'axios'
    import {mapState} from 'vuex'
    import { getLocalUserInfo } from '@/api/login';
    import { getApiUrl } from '@/api/settings';

    export default {
      components: {
        sidebarMenu,
        breadcrumbNav,
        sidebarMenuShrink
      },
      data() {
        return {
          formItem: {
            password: ''
          },
          buttonLoading: false,
          showReLogin: false,
          spanLeft: 4,
          spanRight: 20,
          currentPageName: '',
          userName: sessionStorage.getItem('user'),
          isFullScreen: false,
          lockScreenSize: 0,
          colorList: ['#f56a00', '#7265e6', '#ffbf00', '#00a2ae'],
          color: '',
        }
      },
      computed: {
        ...mapState([
          'pageOpenedList',
          'currentPath',
          'menuList'
        ]),
        stmt: {
          get() {
            return this.$store.state.stmt
          },
          set(val) {
            this.$store.state.stmt = val
          }
        },
        hideMenuText: function () {
          return this.$store.state.hideMenuText
        }
      },
      methods: {
        toggleClick () {
          this.$store.state.hideMenuText = !this.$store.state.hideMenuText
          sessionStorage.setItem('hideMenuText', this.$store.state.hideMenuText ? '1' : '0')
        },
        redirectLogin() {
          this.handleClickUserDropdown()
        },
        reLogin() {
          this.buttonLoading = true;
          getLocalUserInfo(sessionStorage.getItem('user'), this.formItem.password).then(res => {
            if (res.data.status === 'success') {
              this.$refs['loginForm'].resetFields()
              this.$Message.success({content: '重新登录成功'});
              sessionStorage.setItem('user', res.data.data.username);
              sessionStorage.setItem('token', res.data.token);
              sessionStorage.setItem('auth', res.data.data.role);
              sessionStorage.setItem('real_name', res.data.data.username_chn);
              sessionStorage.setItem('tenant_name', res.data.data.tenant);
              if (res.data.data.role === 'admin') {
                sessionStorage.setItem('access', 1)
              } else {
                sessionStorage.setItem('access', 0)
              }

              sessionStorage.setItem('department', res.data.data.department ? res.data.data.department: '无');
              sessionStorage.setItem('email', res.data.data.email);
              this.showReLogin = false
              this.buttonLoading = false;
            } else {
              this.$config.auth_notice('用户不存在或用户名密码错误。')
            }
          }).catch(err => {
            this.$config.auth_notice(err)
          }).finally(() => {
            this.buttonLoading = false;
          });
        },
        // 个人中心
        handleClickUserDropdown() {
          localStorage.clear()
          sessionStorage.clear()
          if (process.env.VUE_APP_IS_PASSPORT === '1') {
            if (window.location.origin.indexOf('pallas-test') >= 0) {
              location.href = `${process.env.VUE_APP_LUFAX_SSO_URL}passport/account/logout?redirect=${process.env.VUE_APP_LUFAX_TEST_BASE_URL}`;
            } else if (window.location.origin.indexOf('devops') >= 0) {
              location.href = `${process.env.VUE_APP_LUFAX_SSO_URL}passport/account/logout?redirect=${process.env.VUE_APP_LUFAX_BASE_URL}`;
            } else if (window.location.origin.indexOf('lujs') >= 0) {
              location.href = `${process.env.VUE_APP_LUJS_SSO_URL}passport/account/logout?redirect=${process.env.VUE_APP_LUJS_BASE_URL}`;
            } else if (window.location.origin.indexOf('trust') >= 0) {
              location.href = `${process.env.VUE_APP_TRUST_SSO_URL}passport/account/logout?redirect=${process.env.VUE_APP_TRUST_BASE_URL}`;
            } else if (window.location.origin.indexOf('172') >= 0) {
              location.href = `${process.env.VUE_APP_DEV_SSO_URL}passport/account/logout?redirect=${process.env.VUE_APP_DEV_BASE_URL}`;
            } else if (window.location.origin.indexOf('5.6') >= 0) {
              location.href = `${process.env.VUE_APP_DEV_SSO_URL}passport/account/logout?redirect=${process.env.VUE_APP_DEV_BASE_URL}`;
            } else if (window.location.origin.indexOf('.94.') >= 0) {
              location.href = `${process.env.VUE_APP_PRE_SSO_URL}passport/account/logout?redirect=${process.env.VUE_APP_PRE_BASE_URL}`;
            } else if (window.location.origin.indexOf('30.23') >= 0) {
              location.href = `${process.env.VUE_APP_OA_PRE_SSO_URL}passport/account/logout?redirect=${process.env.VUE_APP_OA_PRE_BASE_URL}`;
            } else {
              this.$Message.error({content: '未登记的登录环境，请联系管理员', duration: 15})
            }
          } else {
            this.$router.push({
              name: 'login'
            })
          }

        },
        handleChange() {
          // this.color = this.colorList[i]
          this.color = '#7265e6'
        },
        init() {
        },
        beforeunloadFn() {
          this.$store.commit('snippetTagToJson')
        }
      },
      mounted() {
        this.handleChange();
        this.$store.commit('Breadcrumbset', this.$route.matched[1].name)
        this.$store.state.currentPageName = this.$route.matched[1].name
        if (localStorage.getItem('pageOpenedList')) {
          this.$store.state.pageOpenedList = JSON.parse(localStorage.getItem('pageOpenedList'))
        } else {
          this.$store.state.pageOpenedList = [{
            title: '首页',
            path: '',
            name: 'home_index'
          }]
        }
        this.init();
        // eslint-disable-next-line
        console.log(process.env)
        this.bus.$on('errorHandler', (e) => {
          console.log(e)
          if (e.response !== undefined) {
            if (e.response.status === 401) {
              this.showReLogin = true
            } else if (e.response.status === 403) {
              this.$Message.error({content: '无权限访问。', duration: 5})
            } else {
              this.$Message.error({content: '请求异常，请稍后重试', duration: 5})
            }
          } else {
            this.$Message.error({content: '请求异常，请稍后重试', duration: 5})
          }
        });
        this.$store.state.hideMenuText = sessionStorage.getItem('hideMenuText') === '1'
      },
      created() {
        // 权限菜单过滤相关
        this.$store.commit('Menulist');
        this.$store.commit('snippetTagFromJson');
        window.addEventListener('beforeunload', e => this.beforeunloadFn(e))
      },
      destroyed() {
        this.bus.$off('errorHandler');
        window.removeEventListener('beforeunload', e => this.beforeunloadFn(e))
      }
    }
</script>
