<style>

  .btn_hover {
    width: 60px;
    margin-left: 34%;
    padding: 10px 0;
  }

</style>

<template>
  <div>
    <div style="padding:0 5px"><img src="../assets/logo_p.png" width="50"></div>
    <template v-for="(item, index) in menuList">
      <Dropdown v-if="item.children.length >= 1 && item.name !== 'main' && !item.hide" placement="right-start" :key="index"
                @on-click="changeMenu">
        <div class="btn_hover">
          <Icon size="20" :color="iconColor" :type="item.icon"></Icon>
        </div>
        <DropdownMenu style="width: 200px;" slot="list">
          <template v-for="child in item.children">
            <template v-if="filtermenulist[child.name] === '1' && !child.hide">
              <DropdownItem :name="child.name" :key="child.title">
                <Icon :type="child.icon" size="20"></Icon>
                <span style="padding-left:10px;">{{ child.title }}</span>
              </DropdownItem>
            </template>
          </template>
        </DropdownMenu>
      </Dropdown>
      <Dropdown v-else placement="right-start" :key="index" @on-click="changeMenu">
        <div @click="changeMenu(item.children[0].name)" class="btn_hover">
          <Icon :size="20" :color="iconColor" :type="item.icon"></Icon>
        </div>
        <DropdownMenu style="width: 200px;" slot="list">
          <DropdownItem :name="item.children[0].name" :key="item.children[0].title">
            <Icon :type="item.icon" size="20"></Icon>
            <span style="padding-left:10px;">{{ item.children[0].title }}</span>
          </DropdownItem>
        </DropdownMenu>
      </Dropdown>
    </template>
    <Dropdown placement="right-start" @on-click="changeMenu">
      <div @click="changeMenu('login')" class="btn_hover">
        <Icon type="md-log-out" size="20" :color="iconColor"></Icon>
      </div>
      <DropdownMenu slot="list">
        <DropdownItem name="login" key="login">退出</DropdownItem>
      </DropdownMenu>
    </Dropdown>
  </div>
</template>

<script>

  export default {
    name: 'sidebarMenuShrink',
    props: {
      menuList: {
        type: Array
      },
      iconColor: {
        type: String,
        default: '#515a6e'
      }
    },
    data () {
      return {
        currentPageName: this.$route.name,
        openedSubmenuArr: this.$store.state.openedSubmenuArr,
        filtermenulist: {
          'task_list': '1',
          'task_create': '1',
          'management-user': '1',
          'management-database': '1',
          'management': '1',
          'setting': '1',
          'task': '1'
        }
      }
    },
    methods: {
      changeMenu (active) {
        if (active === 'login') {
          localStorage.clear()
          sessionStorage.clear()
          this.$router.push({
            name: active
          })
        } else {
          this.$config.openPage(this, active)
        }
      }
    },
    created () {
    }
  }
</script>
